import React from 'react';
import { Card, Row, Col } from 'antd';
import { ExperimentOutlined, BookOutlined } from '@ant-design/icons';
import './Home.css';

const Home: React.FC = () => {
  return (
    <div className="home-container">
      <h1>   Interactive Physics</h1>
      <p className="intro-text">
               
      </p>
      
      <Row gutter={[16, 16]} className="features-section">
        <Col xs={24} sm={12}>
          <Card 
            title={
              <span>
                <ExperimentOutlined /> 
              </span>
            }
          >
                      
          </Card>
        </Col>
        <Col xs={24} sm={12}>
          <Card 
            title={
              <span>
                <BookOutlined /> 
              </span>
            }
          >
                    
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Home; 